.posterbody{
    width: 100%;
    color: white;
}

.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}

.posters::-webkit-scrollbar{
    display: none;
}

.posterImg{
    max-height: 180px;
    margin-right: 30px;
    cursor: pointer;
    transition: transform 200ms ease;
}

.posterImg:hover{
    transform: scale(1.2);
}

.largeposterImg{
    max-height: 250px;
    margin-right: 40px;
    transition: transform 200ms ease;
}

.largeposterImg:hover{
    transform: scale(1.2);
}

